html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, dialog, figcaption, figure,
footer, header, hgroup, menu, command, nav, section, summary,
time, mark, embed, audio, video, source,
progress, meter {
  background: transparent;
  border: 0 none;
  font-size: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: top; }

fieldset, form, label, legend, keygen, input, output, textarea, datalist {
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: top; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

table,
table th,
table td {
  padding: 0;
  border: none;
  border-collapse: collapse; }

img,
embed {
  vertical-align: top; }

.videoContainer article {
  border-bottom: solid 4px #ffa5a5;
  margin-bottom: 30px;
  word-break: break-all; }

.videoContainer .videoPlayer {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
  overflow: hidden;
  background: url(../img/spinner.gif) no-repeat center center;
  background-size: 40px 40px; }
  .videoContainer .videoPlayer iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

/* clearfix */
.clearfix:after {
  content: "";
  display: block;
  clear: both; }
